<template>
	<div class="InventoryList">
		<header><i class="iconfont icon-xiangzuo1" @click="sendParams"></i><span>库存列表</span></header>
		<div class="container">
			<div>
				<span  @click="SortArr('dataList','sku','SortCondition1',$event)"><span>商品代码</span><i class="iconfont"></i></span>
				<span  @click="SortArr('dataList','qty','SortCondition2',$event)"><span>数量</span><i class="iconfont"></i></span>
				<span  @click="SortArr('dataList','loc','SortCondition3',$event)"><span>库位</span><i class="iconfont"></i></span>
			</div>
			<div v-for='(dataItem,index) in dataList'  :id="index"  :class='{selectDiv:(index+1)==nowData}'  :key='index'  @click="selectData(index)">
				<div>
					<span>{{dataItem.sku}}</span>
					<span>{{dataItem.qty}}</span>
					<span>{{dataItem.loc}}</span>
				</div>
				<div><i class="iconfont icon-xiangyou1"></i></div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"InventoryList",
		data(){
			return{
				nowData:0,//具体为第几条数据
				dataList:[],//数据列表
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
			}
		},
		created(){

			this.nowData = this.$route.query.nowData;

			var dataList = this.$store.state.QueryListData;

			this.dataList = dataList;
		},
		methods:{
			// 返回上一页 传参
			sendParams:function(){

				var that = this;

				this.$router.push({

					name:"InventoryDetails",
					query:{
						nowData:that.nowData
					}
				})
			},
			//选中具体数据
			selectData:function(index){

				this.nowData = index+1;

				this.$router.push({

					name:"InventoryDetails",
					query:{
						nowData:index+1
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.InventoryList{
		&>.container{
			font-size: 30/50rem;
			&>div{
				overflow: hidden;
				width:100%;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>div{
					float: left;
					&>span{
						float:left;
					}
					&>span:nth-child(1){
						width:250/50rem;
					}
					&>span:nth-child(2){
						width:150/50rem;
					}
					&>span:nth-child(3){
						width:230/50rem;
					}
				}
				&>div:nth-child(1){
					width:630/50rem;
					margin-left: 20/50rem;
				}
				&>div:nth-child(2){
					color: #285DCE;
					width:50/50rem;
				}
				i{
					font-size: 29/50rem;
				}
			}
			&>div:nth-child(1){
				&>span{
					float:left;

					span{
						float:left;
						text-align: right;
					}
				}
				&>span:nth-child(1){
					width:250/50rem;
					margin-left: 20/50rem;
					span{
						width: 190/50rem;
					}
				}
				&>span:nth-child(2){
					width:150/50rem;
					span{
						width: 105/50rem;
					}
				}
				&>span:nth-child(3){
					width:230/50rem;
					span{
						width: 135/50rem;
					}
				}
				.iconfont{
					font-size: 45/50rem;
					float: left;
				}
			}
			&>div.selectDiv{
				background-color:rgba(0,0,0,0.08);
			}
		}
	}
	@media screen and (max-width: 320px){

		.InventoryList{
			&>.container{
				&>div:nth-child(1){
					&>span:nth-child(2){
						span{
							width: 90/50rem;
						}
					}
					.iconfont{
						font-size: 50/50rem;
					}
				}
			}
		}
	}
	@media screen and (max-width: 374px) {	
		.InventoryList{
			&>.container{
				&>div{
					border-bottom: 3/50rem solid #999;
				}
			}
		}
	}
</style>


